import React from 'react';
import classnames from 'classnames';
import { SoundOutlined, SoundFilled } from '@ant-design/icons';
import { useTomatoStore } from '@/stores';
import Button from '../Button';
import styles from './index.less';

interface IProp {
  className?: string;
};

/* 声音 */
const AudioBtn = ({ className }: IProp) => {
  const needAudio = useTomatoStore((state) => state.needAudio);
  const setNeedAudio = useTomatoStore((state) => state.setNeedAudio);

  // 设置切换开启声音
  const handleUpdateSound = () => {
    setNeedAudio(!needAudio);
  };

  return (
    <Button className={classnames(styles.audioBtn, className)} onClick={handleUpdateSound}>
      {needAudio ? <SoundFilled /> : <SoundOutlined />}
    </Button>
  );
};

export default AudioBtn;
