/* eslint-disable no-unused-expressions */
import { StateCreator } from 'zustand';
import { defaultWorkTimes, defaultWorkTime, defaultThemeList, defaultThemeIndex } from './const';

interface ITheme {
  type: string;
  title: string;
  audioSrc: string;
  imageSrc: string;
}

export interface TomatoSlice {
  browserTitle: string;
  workTimes: number[];
  workTime: number;
  setWorkTime: (workTime: number) => void;
  time: number;
  setTime: (workTime: number) => void;
  runningType: 'running' | 'wait' | 'stop';
  setRunningType: (runningType: 'running' | 'wait' | 'stop') => void;
  needNotification: boolean;
  setNeedNotification: (needNotification: boolean) => void;
  pushNotification: boolean;
  setPushNotification: (pushNotification: boolean) => void;
  themeList: ITheme[];
  currentThemeIndex: number;
  setCurrentThemeIndex: (currentThemeIndex: number) => void;
  needAudio: boolean;
  setNeedAudio: (needAudio: boolean) => void;
  audio: HTMLAudioElement;
  initAudio: () => void;
  setAudioSrc: (audioSrc: string) => void;
  playAudio: () => void;
  pauseAudio: () => void;
};

export const createTomatoSlice: StateCreator<TomatoSlice> = (set) => ({
  browserTitle: window.document.title,
  workTimes: defaultWorkTimes,
  workTime: defaultWorkTime,
  setWorkTime: (workTime) => (set({ workTime })),
  time: defaultWorkTime * 60,
  setTime: (time) => (set({ time })),
  runningType: 'stop',
  setRunningType: (runningType) => (set({ runningType })),
  needNotification: true,
  setNeedNotification: (needNotification) => (set({ needNotification })),
  pushNotification: false,
  setPushNotification: (pushNotification) => (set({ pushNotification })),
  themeList: defaultThemeList,
  currentThemeIndex: defaultThemeIndex,
  setCurrentThemeIndex: (currentThemeIndex) => (set({ currentThemeIndex })),
  needAudio: false,
  setNeedAudio: (needAudio) => (set({ needAudio })),
  audio: new Audio(),
  initAudio: () => (set(state => {
    state.audio.loop = true;
    state.setAudioSrc(state.themeList[state.currentThemeIndex].audioSrc);
    return state;
  })),
  setAudioSrc: (audioSrc) => (set(state => { state.audio.src = audioSrc; return state; })),
  playAudio: () => (set(state => { state.needAudio && state.audio.paused && state.audio.play(); return state; })),
  pauseAudio: () => (set(state => { !state.audio.paused && state.audio.pause(); return state; })),
});
