import React, { useEffect } from 'react';
import DefalutPanel from '@/module/view/DefalutPanel';
import MiniPanel from '@/module/view/MiniPanel';
import Popup from '@/components/Popup';
import Notification from '@/module/components/Notification';
import { useTomatoStore } from '@/stores';
import styles from './app.less';

function App({ view, entryUrl }: any) {
  const initAudio = useTomatoStore((state) => state.initAudio);
  useEffect(() => {
    initAudio();
  }, []);
  return (
    <>
      <Notification />
      {(view === 'showroom') ? (
        <Popup
          width={1024}
          panel={() => <DefalutPanel />}
          link={entryUrl}
          needTrack
        >
          <div className={styles.main}><MiniPanel /></div>
        </Popup>
      ) : <DefalutPanel />}
    </>
  );
}

export default App;
