import React from 'react';
import classnames from 'classnames';
import { SettingFilled } from '@ant-design/icons';
import Popup from '@/components/Popup';
import { useTomatoStore } from '@/stores';
import Button from '../Button';
import SettingPanel from '../SettingPanel';
import styles from './index.less';

interface IProp {
  className?: string;
};

/* 设置 */
const SettingBtn = ({ className }: IProp) => {
  const runningType = useTomatoStore((state) => state.runningType);
  if (runningType === 'running') {
    return (
      <Button className={classnames(styles.settingBtn, styles.disabled, className)}>
        <SettingFilled />
      </Button>
    );
  }
  return (
    <Popup
      width={360}
      height={280}
      bgCoverStyles={{
        background: 'rgba(255, 255, 255, 0.1)',
        backdropFilter: 'blur(10px)',
      }}
      panel={() => <SettingPanel />}
      needMaximize={null}
    >
      <Button className={classnames(styles.settingBtn, className)}>
        <SettingFilled />
      </Button>
    </Popup>
  );
};

export default SettingBtn;
