import React, { useRef, useEffect, useState } from 'react';
import classnames from 'classnames';
import { Carousel } from 'antd';
import { useTomatoStore } from '@/stores';
import styles from './index.less';

interface IProp {
  className?: string;
};

/* 切换 */
const Cover = ({ className }: IProp) => {
  const themeList = useTomatoStore((state) => state.themeList); // 原始主题列表数据
  const currentThemeIndex = useTomatoStore((state) => state.currentThemeIndex); // 当前主题下标(原始)
  const coverCarouselRef: any = useRef(null);
  const [initLoading, setInitLoading] = useState(true);

  useEffect(() => {
    coverCarouselRef.current.goTo(currentThemeIndex);
  }, [currentThemeIndex]);

  useEffect(() => {
    setInitLoading(false);
  }, []);

  return (
    <Carousel
      dots={false}
      ref={coverCarouselRef}
      className={classnames(styles.cover, !initLoading && styles.show)}
    >
      {themeList.map((item: any, index: any) => (
        <div key={index} className={styles.coverPanel}>
          <div style={{ backgroundImage: `url(${item.imageSrc})` }} />
        </div>
      ))}
    </Carousel>
  );
};

export default Cover;
