import React, { useState } from 'react';
import classnames from 'classnames';
import { Button, InputNumber, Space } from 'antd';
import { useTomatoStore } from '@/stores';
import styles from './index.less';

interface IProp {
  className?: string;
};

const SettingPanel = ({ className }: IProp) => {
  const workTimes = useTomatoStore((state) => state.workTimes);
  const workTime = useTomatoStore((state) => state.workTime);
  const setWorkTime = useTomatoStore((state) => state.setWorkTime);
  const [tempWorkTime, setTempWorkTime] = useState<number>(workTime);

  return (
    <div className={classnames(styles.settingPanel, className)}>
      <div className={styles.timeBox}>
        {workTimes.map(i => (
          <div key={i} className={classnames(styles.timeBoxItem, workTime === i && styles.active)} onClick={() => setWorkTime(i)}>
            <b>{`${i}`}</b>
            <span>分钟</span>
          </div>
        ))}
      </div>
      <div className={styles.timeInput}>
        <Space.Compact block size="small">
          <InputNumber
            size="large" min={1} max={999} value={tempWorkTime}
            formatter={(value) => `${value}分钟`}
            parser={(value) => value?.replace('分钟', '') as unknown as number}
            onChange={(value) => setTempWorkTime(value ? +value : 0)}
          />
          <Button size="large" onClick={() => setWorkTime(tempWorkTime)}>确定</Button>
        </Space.Compact>
      </div>
    </div>
  );
};

export default SettingPanel;
