import React from 'react';
import classnames from 'classnames';
import { CaretRightOutlined, PauseOutlined } from '@ant-design/icons';
import { useTomatoStore } from '@/stores';
import Button from '../Button';
import styles from './index.less';

interface IProp {
  className?: string;
};

const ActionBtns = ({ className }: IProp) => {
  const runningType = useTomatoStore((state) => state.runningType);
  const setRunningType = useTomatoStore((state) => state.setRunningType);

  // 开始
  const handleStart = () => {
    setRunningType('running');
  };

  // 暂停/恢复
  const handleSwitch = () => {
    setRunningType(runningType === 'wait' ? 'running' : 'wait');
  };

  // 结束
  const handleFinish = async () => {
    setRunningType('stop');
  };

  return (
    <div className={classnames(styles.actionBtns, className)}>
      {runningType === 'stop' ? (
        <Button key="start" className={styles.startBtn} onClick={handleStart}>开 始</Button>
      ) : (
        <>
        {/* {runningType === 'running' ? '暂停' : '恢复'} */}
          <Button key="switch" className={styles.iconBtn} onClick={handleSwitch}>{runningType === 'running' ? <PauseOutlined /> : <CaretRightOutlined />}</Button>
          <Button key="finish" className={styles.iconBtn} onClick={handleFinish}>
            <div className={styles.stopIcon} />
          </Button>
        </>
      )}
    </div>
  );
};

export default ActionBtns;
