import React, { useEffect } from 'react';
import { notification } from 'antd';
import { HistoryOutlined } from '@ant-design/icons';
import { useTomatoStore } from '@/stores';

const Notification = () => {
  const [api, contextHolder] = notification.useNotification();
  const needNotification = useTomatoStore((state) => state.needNotification);
  const pushNotification = useTomatoStore((state) => state.pushNotification);
  const setPushNotification = useTomatoStore((state) => state.setPushNotification);

  useEffect(() => {
    if (pushNotification && needNotification) {
      api.open({
        message: '温馨提示',
        description: '工作完成了！休息一下。',
        icon: <HistoryOutlined style={{ color: '#52c41a' }} />,
      });
      setPushNotification(false);
    }
  }, [pushNotification]);

  return contextHolder;
};

export default Notification;
