import React, { useEffect } from 'react';
import classnames from 'classnames';
import { useTomatoStore } from '@/stores';
import { formatTime } from '../../utils/tool';

interface IProp {
  className?: string;
  type?: 'default' | 'mini';
};

const Time =({ className, type = 'default' }: IProp) => {
  const workTime = useTomatoStore((state) => state.workTime);
  const time = useTomatoStore((state) => state.time);
  const setTime = useTomatoStore((state) => state.setTime);
  const browserTitle = useTomatoStore((state) => state.browserTitle);
  const runningType = useTomatoStore((state) => state.runningType);
  const setRunningType = useTomatoStore((state) => state.setRunningType);
  const setPushNotification = useTomatoStore((state) => state.setPushNotification);
  const playAudio = useTomatoStore((state) => state.playAudio);
  const pauseAudio = useTomatoStore((state) => state.pauseAudio);

  useEffect(() => {
    if (runningType === 'stop') {
      setTime(workTime * 60);
    }
  }, [workTime, runningType]);

  // 更新计时器
  useEffect(() => {
    const tick = () => {
      if (time > 0) {
        setTime(time - 1);
      } else {
        setPushNotification(true);
        setRunningType('stop');
        setTime(workTime * 60);
        document.title = browserTitle;
      }
    };
    let timer: any;
    if (runningType !== 'stop') {
      document.title = `${formatTime(time)} - 番茄时钟`;
    }
    if (runningType === 'running') {
      timer = setTimeout(tick, 1000);
      playAudio();
    } else {
      pauseAudio();
    }
    if (runningType === 'stop' && time !== workTime * 60) {
      setTime(workTime * 60);
    }
    return () => {
      if (timer) {
        clearTimeout(timer);
      }
      document.title = browserTitle;
      pauseAudio();
    };
  }, [runningType, time]);

  return (
    <div className={classnames(className)}>{formatTime(time)}</div>
  );
};

export default Time;
