import React from 'react';
import classnames from 'classnames';
import { NotificationOutlined, NotificationFilled } from '@ant-design/icons';
import { useTomatoStore } from '@/stores';
import Button from '../Button';
import styles from './index.less';

interface IProp {
  className?: string;
};

/* 通知 */
const NotificationBtn = ({ className }: IProp) => {
  const needNotification = useTomatoStore((state) => state.needNotification);
  const setNeedNotification = useTomatoStore((state) => state.setNeedNotification);

  // 设置切换开启通知
  const handleUpdateNotification = () => { setNeedNotification(!needNotification); };

  return (
    <Button className={classnames(styles.notificationBtn, className)} onClick={handleUpdateNotification}>
      {needNotification ? <NotificationFilled /> : <NotificationOutlined /> }
    </Button>
  );
};

export default NotificationBtn;
