import React, { useState } from 'react';
import classnames from 'classnames';
import { Pagination } from 'antd';
import { useTomatoStore } from '@/stores';
import styles from './index.less';

interface IProp {
  className?: string;
};

/* 切换 */
const SwitchTheme = ({ className }: IProp) => {
  const themeList = useTomatoStore((state) => state.themeList); // 原始主题列表数据
  const currentThemeIndex = useTomatoStore((state) => state.currentThemeIndex); // 当前主题下标(原始)
  const setCurrentThemeIndex = useTomatoStore((state) => state.setCurrentThemeIndex); // 当前主题内容
  // const [list] = useState([themeList[themeList.length - 1]].concat(themeList).concat([themeList[0]]));
  const [currentPage, setCurrentPage] = useState(currentThemeIndex + 2);
  const setAudioSrc = useTomatoStore((state) => state.setAudioSrc); // 修改音频url

  // 显示转换的中文
  const itemRender = (page: number, type: string, originalElement: React.ReactNode) => {
    if (type === 'page' && page === currentThemeIndex + 2) {
      return themeList[currentThemeIndex].title;
    } else if (['prev', 'next'].includes(type)) {
      return originalElement;
    }
    return null;
  };

  // 循环逻辑
  const handleChangePage = (page: number) => {
    let index = page - 2;
    if (page === 1) {
      // 如果是补位后的首位1 则对应原始数据的最后一位
      index = themeList.length - 1;
      page = themeList.length + 1;
    } else if (page === themeList.length + 2) {
      // 如果是补位后的末尾位 则对应原始数据的第一位
      index = 0;
      page = 2;
    }
    setCurrentThemeIndex(index);
    setCurrentPage(page);
    setAudioSrc(themeList[index].audioSrc);
  };

  return (
    <Pagination
      className={classnames(styles.switchTheme, className)}
      size="small"
      total={themeList.length + 2} // 前后各补1位
      pageSize={1}
      showTitle={false}
      itemRender={itemRender}
      onChange={handleChangePage}
      current={currentPage}
    />
  );
};

export default SwitchTheme;
